  --bs-body-color: #a6b0cf;
  --bs-heading-color: #f6f6f6;
  --bs-border-color: #32394e;
  --bs-custom-white: #2a3042;
  --bs-input-border-color: #32394e;
  --bs-input-focus-border-color: #3a425a;
