  --bs-nav-tabs-link-active-border-color: #3a425a;
  --bs-nav-tabs-border-color: #3a425a;
  --bs-nav-tabs-link-hover-border-color: #3a425a;
  --bs-nav-tabs-link-active-color: #556ee6;
}

[data-layout-mode="dark"] .accordion {
  --bs-accordion-active-bg: rgba(85, 110, 230, 0.1);
}

[data-layout-mode="dark"] .toast {
  --bs-toast-header-bg: #2a3042;
  --bs-toast-header-color: #c3cbe4;
  --bs-toast-bg: #2a3042;
}

[data-layout-mode="dark"] .table {
  --bs-table-striped-bg: rgba(191, 200, 226, 0.05);
  --bs-table-hover-bg: rgba(191, 200, 226, 0.05);
  --bs-table-active-bg: rgba(191, 200, 226, 0.05);
}
body {
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: cover;
}
[data-layout-mode="dark"] .table-light {
  --bs-table-color: #a6b0cf;
  --bs-table-bg: #32394e;
  --bs-table-border-color: #32394e;
}

[data-layout-mode="dark"] .table-dark {
  --bs-table-color: #a6b0cf;
  --bs-table-bg: #32394e;
  --bs-table-border-color: #3a425a;
}

#page-topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  /* padding: 15px 21px 26px 274px; */
  z-index: 1002;
  background-image: #1C1B19;
  /* background-color: var(--bs-heading-bg); */
  -webkit-box-shadow: 0 0.75rem 1.5rem rgba(18, 38, 63, 0.03);
  box-shadow: 0 0.75rem 1.5rem rgba(18, 38, 63, 0.03);
}

.navbar-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  -webkit-box-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin: 0 auto;
  /* height: 70px; */
  padding: 10px;
  /* padding: 0 calc(24px * 0.5) 0 0; */
  background: #000;
  /* border-radius: 15px; */
}

.navbar-header .dropdown .show.header-item {
  background-color: var(--bs-gray-100);
}

.navbar-brand-box {
  padding: 0 1.5rem;
  text-align: center;
  width: 250px;
}

.logo {
  /* line-height: 70px; */
}

.logo .logo-sm {
  display: none;
}

.logo-light {
  display: none;
}

.app-search {
  padding: calc(32px * 0.5) 0;
}

.app-search .form-control {
  border: none;
  height: 38px;
  padding-left: 40px;
  padding-right: 20px;
  background-color: var(--bs-topbar-search-bg);
  -webkit-box-shadow: none;
  box-shadow: none;
  border-radius: 30px;
}

.app-search span {
  position: absolute;
  z-index: 10;
  font-size: 16px;
  line-height: 38px;
  left: 13px;
  top: 0;
  color: var(--bs-gray-600);
}

.megamenu-list li {
  position: relative;
  padding: 5px 0;
}

.megamenu-list li a {
  color: var(--bs-body-color);
}

@media (max-width: 992px) {
  .navbar-brand-box {
    width: 100%;
    padding: 0;
    text-align: start;
    margin-left: 13px;
  }
  .logo span.logo-lg {
    display: none;
  }
  .logo span.logo-sm {
    display: inline-block;
  }
}

.page-content {
  padding: calc(90px + 24px) calc(24px * 0.75) 60px calc(24px * 0.75);
}

.header-item {
  height: 70px;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  color: #fff;
  border: 0;
  border-radius: 0;
}

.header-item:hover {
  color: var(--bs-header-item-color);
}

.header-profile-user {
  height: 36px;
  width: 36px;
  background-color: var(--bs-gray-300);
  padding: 3px;
}

.noti-icon i {
  font-size: 22px;
  color: var(--bs-header-item-color);
}

.noti-icon .badge {
  position: absolute;
  top: 12px;
  right: 4px;
}

.notification-item .d-flex {
  padding: 0.75rem 1rem;
}

.notification-item .d-flex:hover {
  background-color: var(--bs-gray-300);
}

.dropdown-icon-item {
  display: block;
  border-radius: 3px;
  line-height: 34px;
  text-align: center;
  padding: 15px 0 9px;
  display: block;
  border: 1px solid transparent;
  color: var(--bs-gray-600);
}

.dropdown-icon-item img {
  height: 24px;
}

.dropdown-icon-item span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.dropdown-icon-item:hover {
  border-color: var(--bs-border-color);
}

.fullscreen-enable [data-bs-toggle="fullscreen"] .bx-fullscreen::before {
  content: "\ea3f";
}

