  background-image: linear-gradient( to right, rgb(0 0 0 / 47%), rgb(24 19 19 / 80%) ), url(https://img.oemoffhighway.com/files/base/acbm/ooh/image/2021/07/AdobeStock_255321623.60f1f5e01fe64.png?auto=format%2Ccompress&q=70);
