.steps {
  position: relative;
  display: flex;
  justify-content: space-between;
  /* width: 200px; */
}
.step {
  width: 60px;
  height: 60px;
  background: #fff;
  border: 2px solid #acaca6;
  border-radius: 50%;
  transition: background 1s;
}
.step.selected {
  border: 2px solid #4b81bd;
}
.step.completed {
  border: 2px solid #4b81bd;
  background: #4b81bd;
}
.progress {
  position: absolute;
  width: 94%;
  top: 48%;
  background: linear-gradient(to right, #F99F36 1%, #00bcd4 50%);
  /* border-bottom: 2px solid black; */
  /* z-index: -1; */
}
